import React from "react"
import { Container, Row, Col, Image } from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLinkedin, faGithub, faTwitter } from '@fortawesome/free-brands-svg-icons'
import PropTypes from 'prop-types';

import ScrollableAnchor from 'react-scrollable-anchor'

const SocialMedia = (props) => {
    const { type, url } = props;
    let Icon = null;
    switch (type) {
        case "github":
            Icon = faGithub;
            break;
        case "linkedin":
            Icon = faLinkedin;
            break;
        case "twitter":
            Icon = faTwitter;
            break;
        default:
            Icon = null;
            break;
    }
    return (
        <li className="list-inline-item">
            <a className={`${type}-btn`} href={url}>
                <FontAwesomeIcon icon={Icon} />
            </a>
        </li>
    )
}

const Member = (props) => {
    const { name, title, socialMedia, img } = props;
    return (
        <Col xs="12" sm="6" md="4">
            <div className="team-member">
                <Image className="rounded-circle mx-auto" src={img} style={{ "backgroundColor": "#C1393D" }} />
                <h4 className="team-name">{name}</h4>
                <p className="text-muted">{title}</p>
                <ul className="list-inline social-buttons">
                    {
                        socialMedia.map((item, id) => {
                            return (
                                <SocialMedia type={item.type} url={item.url} key={id} />
                            )
                        })
                    }
                </ul>
            </div>
        </Col>
    )
}
Member.propTypes = {
    name: PropTypes.string,
    title: PropTypes.string,
    socialMedia: PropTypes.array,

}

const Team = () => {
    return (
        <ScrollableAnchor id="team">
            <section id="team" className="bg-dark">
                <Container>
                    <Row>
                        <Member name="Cameron Steele" title="President" socialMedia={[{ type: "github", url: "https://github.com/ATechAdventurer" }, { type: "linkedin", url: "https://linkedin.com/in/ATechAdventurer" },]} img={require('../dist/img/team/Cameron-Photo.png')} />
                    </Row>
                </Container>
            </section>
        </ScrollableAnchor>
    )
}

export default Team
