import React from "react"
import {Container, Row, Col} from 'react-bootstrap'

class FAQItem extends React.Component {
    constructor(props) {
        super()
        this.state = {
            isOpen: false
        };
    }
    render() {
        return (
            <li>
                <span className="faq-link" onClick={this.onClickHandler} aria-expanded="true">{this.props.question}</span>
                <div className={this.state.isOpen ? "": "collapse"}>
                    <p>
                        {this.props.answer}
                    </p>
                </div>
            </li>
        )
    }
    onClickHandler = () => { 
        this.setState({
            isOpen: !this.state.isOpen
        })
    }
}

const FAQ = (props) => {
    return (
        <section id="faq">
            <Container>
                <Row className="justify-content-center">
                    <Col lg="12" className="text-center">
                        <h2 className="text-uppercase section-heading">FAQ</h2>
                    </Col>
                    <Col lg="9">
                        <ul id="faq-list">
                        <FAQItem question="How much does it cost to use the space?" answer="If you are a student, faculty, or alumni of The University Of Texas at Dallas the space is completely free to use."/>
                        <FAQItem question="Where is the space?" answer="Our address is 3000 Waterview Parkway, Richardson, TX. We are located in the SPN (Synergy Park North) building on the second floor. If you enter from the main entrance go up the right hand stairs and through the glass doors."/>
                        <FAQItem question="Are there any makerspace employees?" answer="Nope we are 100% student run and lead."/>
                        <FAQItem question="Why does it cost to use the 3D Printers?" answer="We aren't subsidised by the school so we have to pay for the filament and machines, to be able to afford to keep these services active we have to charge a small fee."/>
                        <FAQItem question="How can I teach a workshop?" answer="Just send us an email at teach@utdmaker.space"/>
                        <FAQItem question="What equipment does the space have?" answer="A regularly updated list of equipment can be found at wiki.utdmaker.space/equipment"/>
                        <FAQItem question="How can I have access? " answer="The only way you can get access to SPN is by taking the online training course https://utd.bioraft.com, completing the five trainings in the fabrication shop and volunteering 3 hours a week. You can get more information about this downstairs in the fabrication shop or emailing Gene Woten the shop manager at woten@utdallas.edu."/>
                        <FAQItem question="What is a Makerspace? " answer="A Makerspace is a place where people can share knowledge and tools to create. The possibilities are limitlessâ€”arts and crafts, electronics, 3D printing. The only limit is the ingenuity of our makerspace's members!"/> 
                        </ul>
                    </Col>
                </Row>
            </Container>
        </section>
    )
}

export default FAQ
